<template>
<div>
  <b-container fluid>
           <!--   table -->
    <b-row style="padding:10px;">
    <!--   -->
      <b-col md="6">
        <h2 align="left"> </h2>
      </b-col>
    </b-row>
  <div>
    <b-table striped hover :items="indvInfoList"  :fields="filteredFields" @row-clicked="handleRowClick">
      <template #cell(score)="data">
        {{ (parseFloat(data.value)).toFixed(2) + "%" }}
      </template>
    </b-table>
  </div>
  <b-modal v-model="showModal" :title="' '" @hide="handleModalClose">
    <ul>
      <li><strong>[  ]</strong> {{ modalData.c }}</li>
      <li><strong>[  ]</strong> {{ modalData.e }}</li>
      <li><strong>[  ]</strong> {{ modalData.f }}</li>
      <li><strong>[  ]</strong> {{ modalData.g }}</li>
      <li><strong>[  ]</strong> {{ modalData.h }}</li>
      <li><strong>[  ]</strong> {{ modalData.i }}</li>
      <li><strong>[   ]</strong> {{ modalData.score }}</li>
    </ul>
  </b-modal>
</b-container>
</div>    
  </template>
  
  <script>
  import http from "../http-common";
  import { BModal } from 'bootstrap-vue'
  import 'bootstrap/dist/css/bootstrap.css'
  import 'bootstrap-vue/dist/bootstrap-vue.css'


  export default {
    name: 'CandidateMatching',
    components: {
      BModal,
    },

    data() {
      return {
        indvInfoList: [],
        fields: [
          // { key: 'index', label: '#'},
          { key: 'a', label: '' },
          { key: 'b', label: '' },
          { key: 'c', label: '' },
          { key: 'd', label: '' },
          { key: 'e', label: '' },
          { key: 'f', label: '' },
          { key: 'g', label: '' },
          { key: 'h', label: '' },
          { key: 'i', label: '' },
          { key: 'j', label: '' },
          { key: 'k', label: '' },
          { key: 'l', label: '' },
          { key: 'm', label: '' },
          { key: 'n', label: '' },
          { key: 'o', label: '' },
          { key: 'p', label: '  ' },
          { key: 'score', label: ' ' },
          
        ],
        //    
        visibleFields: ['c', 'e', 'f', 'score'],
        clickedRow: null, //    
        modalData: {}, //    
        showModal : false,
      };
    },
    computed: {
    //    visibleFields  fields  computed 
      filteredFields() {
        return this.fields.filter(field => this.visibleFields.includes(field.key));
      },
    },
    methods: {
      fetchIndvInfoList() {
        console.log("hi");
        console.log("comp_id: " + this.$route.params.comp_id);
        // const comp_id = this.$route.params.comp_id; //   
        http.get("/get-candidate",{ params: { comp_id: this.$route.params.comp_id} })
          .then(response => {
            this.indvInfoList = response.data;
          })
          .catch(error => {
            console.error('Error fetching data:', error);
          });
      },
      handleRowClick(item) {
      //     
        this.modalData = {
          c: item.c,
          e: item.e,
          f: item.f,
          g: item.g,
          h: item.h,
          i: item.i,
          score: (parseFloat(item.score)).toFixed(2) + "%"
          //   
        };
        console.log("Row clicked g:", item.g);
        this.clickedRow = item;
        console.log("Clicked Row Data:", this.clickedRow);
        this.showModal = true; //  
        console.log("isOpen?: " + this.showModal);
      },
      handleModalClose() {
        //    clickedRow 
        this.clickedRow = null;
        this.showModal = false; //  
        console.log("isClose?: " + this.showModal);
        this.modalData = {};
      },
    },
    mounted() {
      this.fetchIndvInfoList();
    },
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .hello{
    padding: 30px
  }
  </style>